@import "_includes";

* {
    box-sizing: border-box;
}
html {
    overflow-x: hidden;
    font-size: 62.5%;
    @include desktop-large-below {
        font-size: 60%;
    }
    @include desktop-medium-below {
        font-size: 55%;
    }
    @include desktop-small-below {
        font-size: 52%;
    }
    @include tablet-below {
        font-size: 50%;
    }
    @include phone-large-below {
        font-size: 40%;
    }
}

body {
    background: $grey;
    font-family: $font;
    color: $white;
    font-size: 1.6rem;
    font-feature-settings: "liga" 0;

    @include phone-large-below {
        font-size: 2rem;
    }
}

.wrapper {
    max-width: 1400px;
    width: 100%;
    padding: 0 1.5rem;
    margin: 0 auto;
    // border: solid 10px blue;
}









