.footer {
  width: 100%; }
  .footer .wrapper {
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-justify-content: space-between;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-align-items: center;
        -ms-flex-align: center;
            align-items: center; }

.footer-nav__links, .footer-social__links {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex; }
  .footer-nav__links a, .footer-social__links a {
